$global-column: 120px;
$global-gap: 40px;

@mixin monument-extanded {
	font-family: "MonumentExtended", "Noto Sans KR", sans-serif;
}

@mixin noto-sans-kr {
	font-family: "Noto Sans KR", sans-serif;
}

@mixin color-editor($color, $background-color) {
	background-color: $background-color;
	color: $color;
}

@function column-width($num) {
	@return $global-column * $num + $global-gap * ($num - 1);
}

* {
	margin: 0;
	padding: 0;
}

body {
	font-size: 14px;
	letter-spacing: -0.025em;
}

img {
	max-width: 100%;
	height: auto;
}

.footer {
	&--contentinfo {
		text-align: center;
		color: #cbcbcb;
		width: 100%;
		padding: 4em 0;
		background-color: #303030;
	}
	&__logotype {
		height: 5em;
		margin-bottom: 2em;
	}

	&__copyright {
		font-size: 0.8em;
		letter-spacing: 0.5em;
		opacity: 0.9;
	}
}

// custom pattern stylesheet
.studio-baang {
	&-composition {
		position: relative;
		padding: 130px 0 140px;
		& > .wp-block-group__inner-container {
			display: flex;
			flex-direction: column;
			width: column-width(8);
			margin: 0 auto;
		}
		&__title {
			margin-bottom: 1.5em;
			h2 {
				@include monument-extanded();
				font-weight: 100;
				font-size: 24px;
				letter-spacing: -0.01em;
			}
			.wp-block-group__inner-container {
				overflow: hidden;
			}
			&--float-both {
				position: absolute;
			}
		}
		&__description {
			width: column-width(6);
			p {
				margin-bottom: 2em;
				@include noto-sans-kr;
				font-size: 20px;
				line-height: 2em;
				letter-spacing: -0.025em;
			}
			&--float-both {
				margin-left: auto;
				width: column-width(4);
			}
		}
	}
	&-gallery {
		--wp--style--gallery-gap-default: calc(var(--vh, 1vh) * 50) 0;
		width: column-width(10);
		margin: 0 auto;
		padding: 140px 0;
		overflow: hidden;
		.wp-block-image {
			width: column-width(8);
			&:nth-of-type(2n) {
				margin-left: auto;
			}
		}
	}
	&-variation {
		.wp-block-group__inner-container {
			display: grid;
			grid: auto / repeat(6, 1fr);
			min-height: calc(var(--vh, 1vh) * 100);
			& > div {
				&:first-of-type {
					grid-row: 1 / fill;
					grid-column: 1 / 5;
				}
				&:nth-of-type(2),
				&:nth-of-type(3) {
					grid-column: 5 / fill;
				}
			}
		}
		.wp-block-image {
			width: 25%;
			margin: 0 auto;
		}
	}
	&-cover {
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		height: calc(var(--vh, 1vh) * 100);
		overflow: hidden;
		&__background-color {
			position: absolute;
			top: 0;
			left: 0;
			display: block;
			width: 100%;
			height: 100%;
			z-index: 1;
			&--with-image {
				opacity: 0.5;
			}
		}
		&__image {
			&--cover {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				object-fit: cover;
				pointer-events: none;
			}
			&--logo {
				width: column-width(3);
				z-index: 1;
			}
		}
		&__copyright {
			position: absolute;
			bottom: 1em;
			@include monument-extanded();
			color: #ffffff;
			font-weight: 100;
			mix-blend-mode: exclusion;
			z-index: 1;
		}
	}
}

@media screen and (max-width: 640px) {
	.footer__logotype {
		height: 4em;
	}
}
